import React, { useEffect, useState } from 'react';
import style from '../styles/styles';
import styled from 'styled-components';

//
import gameBackground from '../assets/night.png';

//
import axios from 'axios';
// 
import { io, Socket } from 'socket.io-client';
import GameStart from '../screens/GameStart';

//, 
import { ReactComponent as HumanCharacter } from '../assets/human.svg';
import { ReactComponent as GhostCharacter } from '../assets/purple.svg';
import colors from '../styles/styles';

function GameRoleComponent(props) {

    const ghost = "     Holly!\n..  !     , \n ?\n         !\n       .\n      \n !";
    const human = "    Molly!\n  ?\n       .\n     \n     .\n    !";

    //  5 , 
    const [seconds, setSeconds] = useState(5);

    //let timer = 5;
    useEffect(() => {
        //console.log('   _gamerole' + props.role);
        ///timer = props.timer;

        const countdown = setInterval(() => {
            if (parseInt(seconds) > 0) {
                setSeconds(parseInt(seconds) - 1);
            }
        }, 1000);

        return () => {
            clearInterval(countdown); 
        } ;
    }, [seconds]);

    return (
        <Container>
            <SubContainer>                
                {
                    props.role == "human" ?                        
                    <RoleContainer>
                            <RoleImg>
                                <HumanCharacter className="ghost" width="80" height="117" />
                            </RoleImg>
                            <RoleTitle role="human" color={style.red}>  </RoleTitle>
                            
                            <RoleContent role="human">
                                {human.split("\n").map((i, key) => {
                                  if (key !== 2)
                                        return <text key={key}> {i}</text>;
                                    else
                                        return <p key={key}><RoleTxt color={style.red}>3</RoleTxt>{i}</p>;
                                })}
                            </RoleContent>
                        </RoleContainer>
                        :
                        <RoleContainer>
                            <RoleImg>
                                <GhostCharacter className="ghost" width="80" height="117" />
                            </RoleImg>
                            <RoleTitle role="ghost" color={style.blue}>  </RoleTitle>
                            
                            <RoleContent role="ghost" >
                                {ghost.split("\n").map((i, key) => {
                                  if (key !== 5)
                                        return <text key={key}> {i}</text>;
                                    else
                                        return <p key={key}><RoleTxt color={style.red}>3 </RoleTxt>{i}</p>;
                                })}
                            </RoleContent>
                        </RoleContainer>
                }

                <TimerBtnContainer>{seconds}  </TimerBtnContainer>
            </SubContainer>
        </Container>

    );
}

const Container = styled.div`
    width: 480px;
    height: 500px;
    margin-top: 60px;
    border-width: thin;
    border-radius: 10px;
    border-color: #000000;
    border-style: solid;
    background-color: #ffffff;
`;

const SubContainer = styled.div`
    width: 420px;
    height: 400px;
    padding: 5px;
    margin-left: 20px;
    margin-top: 20px;    
`;

const TimerBtnContainer = styled.div`
    width: 130px;
    height: 30px;
    margin-top: 15px;
    margin-left: 300px;   
    font-size: 20px;
    background-color: #fff;
    text-align: center;
    border-width: 1px;
    border-radius: 15px;
    border-color: #000;    
    border-style: solid;
    margin-bottom: 20px;
`;

const RoleContainer = styled.div`
    width: 100%;
    font-size: 23px;
    display: flex;  
    flex-direction: column;  
    
    p{
        margin-top: 5px;
        margin-bottom: 0px;
        white-space: nowrap;
    }
`;

const RoleTxt = styled.text`
    font-size: 24px;
    color: ${(props) => (props.color)};
`;

const RoleImg = styled.div `
    width: 100%;
    height: 100px;
    display: flex;
    align-items: center;
    justify-content: center;

    .ghost {
        animation: motion 1.5s linear 0s infinite;
        margin-bottom: 0;
    }
`;

const RoleTitle = styled.div`
    text-align: center;   
    color: ${(props) => (props.color)};
    ${(props) => (props.role) === 'ghost' ? ` margin-top: 15px;` : ` margin-top: 20px;`}
`;

const RoleContent = styled.div`
    width: 100%;
    display: flex;     
    flex-direction: column;    
   
    ${(props) => (props.role) === 'ghost' ? `font-size: 20px; margin-top : 20px;` 
            : `font-size: 21px; margin-top : 30px; `}

    text{
        margin-top: 5px;
     }
`;

export default GameRoleComponent;