import React, { createRef, useEffect, useState, useRef } from 'react';
import io from 'socket.io-client';
import styled from 'styled-components';
import style from '../styles/styles';
import RefreshVerification from '../server/RefreshVerification';
//import Toast from '../components/Toast';
import * as _ from 'lodash';

import axios from 'axios';

//RefreshVerification.verification();

import html2canvas from 'html2canvas';
import { useHistory, useLocation } from 'react-router';

let userList = [{}];

const GameDrawing = (props) => {
    const history = useHistory();

    const { gameSetNo, gameIdx, socket, leaderIdx, order, color, room_idx, idx, member_count, role, setIdx, keyword } = props;

    const [possible, setPossible] = useState(true);
    const [seconds, setSeconds] = useState(10); //   
    const [waitSeconds, setWaitSeconds] = useState(-1); //   ,         -1  
    const [secondsLoading, setSecondsLoading] = useState(-1); //     
    //const [readyNextOrder, setReadyNextOrder] = useState(false); //         
    const [reDraw, setReDraw] = useState(false); //    canvas   

    const orderCount = useRef(1); // orderCount
    const drawingTime = useRef(true); //      
    const readyNextOrder = useRef(false); //      

    userList = props.userList;

    // 
    let verify = RefreshVerification.verification()
    //console.log('   t/f  : ' + verify);
    let data, save_token, save_user_idx;

    if (verify === true) {
        data = sessionStorage.getItem('token');
        save_token = JSON.parse(data) && JSON.parse(data).access_token;
        save_user_idx = JSON.parse(data) && JSON.parse(data).user_idx;
    }

    //let user_order = parseInt(order);

    //         useRef  
    const user_order = useRef(0);
    const myList = userList.find((x) => x.user_idx === save_user_idx);
    user_order.current = myList.game_member_order

    let user_color = color;

    //     
    if (user_color === 'RED') {
        user_color = '#FF0000';
    } else if (user_color === 'ORANGE') {
        user_color = '#FF5C00'
    } else if (user_color === 'YELLOW') {
        user_color = '#FFB800'
    } else if (user_color === 'GREEN') {
        user_color = '#95DB3B'
    } else if (user_color === 'BLUE') {
        user_color = '#3B8EDB'
    } else if (user_color === 'PINK') {
        user_color = '#CE3BDB'
    } else {
        user_color = '#823BDB'
    }

    let user_room_index = parseInt(room_idx);
    let user_idx = parseInt(idx);
    let user_member_count = parseInt(member_count);

    let canvas;
    let canvasRef = createRef();

    let pos = {
        drawable: false,
        X: -1,
        Y: -1,
    };

    let oldPos = {
        X: -1,
        Y: -1,
    };

    let ctx;

    useEffect(() => {
        canvas = canvasRef.current;
        ctx = canvas.getContext('2d');

        // DEFAULT   
        ctx.strokeStyle = user_color;
        ctx.lineWidth = 3;

        canvas.addEventListener('mousedown', initDraw); //   
        canvas.addEventListener('mousemove', draw); //  
        canvas.addEventListener('mouseup', finishDraw); //   
        canvas.addEventListener('mouseout', finishDraw); //   
    }, [reDraw]);

    //  
    function initDraw(event) {
        if (orderCount.current === user_order.current && drawingTime.current) {
            //     // props.order
            ctx.beginPath();
            pos = { drawable: true, ...getPosition(event) };
            ctx.moveTo(pos.X, pos.Y);
            oldPos = { X: pos.X, Y: pos.Y };
        }
    }

    //   
    function draw(event) {
        if (pos.drawable) {
            pos = { ...pos, ...getPosition(event) };
            ctx.lineTo(pos.X, pos.Y);
            ctx.stroke();

            //     
            socket.emit('draw', {
                room_idx: user_room_index, // props.room_idx
                draw_info: {
                    color: ctx.strokeStyle,
                    previous_x: oldPos.X,
                    previous_y: oldPos.Y,
                    current_x: pos.X,
                    current_y: pos.Y,
                },
            });

            oldPos = { X: pos.X, Y: pos.Y };
        }
    }

    //   
    function finishDraw() {
        pos = { drawable: false, X: -1, Y: -1 };
    }

    function getPosition(event) {
        return { X: event.offsetX, Y: event.offsetY };
    }

    useEffect(() => {
        socket.on('draw', (data) => {
            //   
            //       
            if (orderCount.current !== user_order.current) {
                // props.order
                ctx.strokeStyle = data.color;
                ctx.beginPath();
                ctx.moveTo(data.previous_x, data.previous_y);
                ctx.lineTo(data.current_x, data.current_y);
                ctx.stroke();
            }
        });

        socket.on('get next turn', (data) => {
            //   
            console.log(data.data); // success 
            readyNextOrder.current = true;
        });

         //   
        socket.on('exit room', (data) => {
            setSeconds(10);
        });

    }, []);

    //   
    useEffect(() => {
        const countdown = setInterval(() => {
            if (parseInt(seconds) > 0) {
                setSeconds(parseInt(seconds) - 1);
            } else if (parseInt(seconds) === 0) {
                //  ,
                console.log('   ');

                drawingTime.current = false; //    
                setPossible(false);
                if (orderCount.current === user_member_count) {
                    clearInterval(countdown);
                    console.log('  !');
                    //   api 
                    saveCanvas();
                    //    
                    setSecondsLoading(10);
                } else {
                    //     
                    socket.emit('send next turn', {
                        room_idx: user_room_index,
                        user_idx: user_idx,
                        member_count: user_member_count,
                        draw_order: orderCount.current
                    });

                    //        3  
                    setWaitSeconds(10);

                    setSeconds(-1);
                    setPossible(false);
                }
            }
        }, 1000);

        return () => {
            clearInterval(countdown);
        };
    }, [seconds]);

    //   
    useEffect(() => {
        const waitcountdown = setInterval(() => {
            if (parseInt(waitSeconds) > 0) {
                setWaitSeconds(parseInt(waitSeconds) - 1);

                if (readyNextOrder.current) {
                    console.log('  ');
                    setWaitSeconds(-1);
                    readyNextOrder.current = false; //     
                    orderCount.current += 1; //  
                    setReDraw(!reDraw); //  
                    drawingTime.current = true;
                    setPossible(true);
                    setSeconds(10);
                }

            } else if (parseInt(waitSeconds) === 0) {
                // 3          
                console.log("readyNextOrder: " + readyNextOrder);
                if (readyNextOrder.current) {
                    console.log('  ');
                    setWaitSeconds(-1);
                    readyNextOrder.current = false; //     
                    orderCount.current += 1; //  
                    setReDraw(!reDraw); //  
                    drawingTime.current = true;
                    setPossible(true);
                    setSeconds(10);
                } else {
                    console.log('   ');
                    alert(' .');
                    history.push({
                        pathname: '/',  
                    });
                    setWaitSeconds(-1);
                }
            }

        }, 1000);

        return () => {
            clearInterval(waitcountdown);
        };
    }, [waitSeconds]);

    //   10 
    useEffect(() => {
        const countdown = setInterval(() => {
            if (parseInt(secondsLoading) > 0) {
                setSecondsLoading(parseInt(secondsLoading) - 1);
            }
            if (parseInt(secondsLoading) === 0) {
                history.push({
                    pathname: '/playingvote/' + room_idx,
                    state: { gameSetNo: gameSetNo, gameIdx: gameIdx, leaderIdx: leaderIdx, move: '10', userList: userList, roomIdx: room_idx, gameSetIdx: setIdx, keyword: keyword, role: role },
                });
                setSecondsLoading(-1);
            }
        }, 1000);

        return () => {
            clearInterval(countdown);
        };
    }, [secondsLoading]);

    const onClick = () => {
        ctx.clearRect(0, 0, canvasRef.current.width, canvasRef.current.height); //  
    };

    const saveCanvas = () => {
        const canvas = document.getElementById('draw');

        const imgBase64 = canvas.toDataURL('image/png', 'image/octet-stream');
        const decodImg = window.atob(imgBase64.split(',')[1]);

        let array = [];
        for (let i = 0; i < decodImg.length; i++) {
            array.push(decodImg.charCodeAt(i));
        }

        var date = + new Date();

        const file = new Blob([new Uint8Array(array)], { type: 'image/png' });
        const fileName = room_idx + '_' + date + '.png';
        let formData = new FormData();

        formData.append('set_image', file, fileName);
        //formData.append('file', file, "21_1202");

        const restURL = 'http://3.17.55.178:3002/game/set/image/' + setIdx; //  

        const reqHeaders = {
            headers: {
                authorization: 'Bearer ' + save_token,
            },
        };

        axios
            .patch(
                restURL, formData,
                reqHeaders
            )
            .then(function (response) {
                console.log('  ');
            })
            .catch(function (error) {
                alert(' error ' + error.message);
            });
    }

    //downloadURI, Save    
    /* function downloadURI(uri, name){
        var link = document.createElement("a")
        link.download = name;
        link.href = uri;
        document.body.appendChild(link);
        link.click();
    } */

    let ImgUrl; //  URL  

    //     
    var currentItem = userList.find((x) => x.game_member_order === orderCount.current);

    //     ( -> )
    /* const sendOrder = () => {
        props.currentOrder(currentItem.user_idx);
    } */

    let cursor_status;
    //     
    const toast = () => {
        if (drawingTime.current === true) {
            if (currentItem.user_idx === save_user_idx) {
                cursor_status = true;
                return <div><Toast> {currentItem.user_name}    .</Toast></div>;
            } else {
                cursor_status = false;
                return <div><Toast> {currentItem.user_name}    .</Toast></div>;
            }
        }
    }

    //     
    let border_user_color = currentItem && currentItem.user_color;

    if (drawingTime.current === true) {
        if (border_user_color === 'RED') {
            border_user_color = '#FF0000';
        } else if (border_user_color === 'ORANGE') {
            border_user_color = '#FF5C00'
        } else if (border_user_color === 'YELLOW') {
            border_user_color = '#FFB800'
        } else if (border_user_color === 'GREEN') {
            border_user_color = '#95DB3B'
        } else if (border_user_color === 'BLUE') {
            border_user_color = '#3B8EDB'
        } else if (border_user_color === 'PINK') {
            border_user_color = '#CE3BDB'
        } else if (border_user_color === 'WHITE') {
            border_user_color = '#FFFFFF'
        } else {
            border_user_color = '#823BDB'
        }
    }
    else {
        border_user_color = 'transparent'
    }

    return (
        <div>
            <div>{toast()}</div>
            {secondsLoading !== -1 ? <TimerToast>  {secondsLoading}  </TimerToast> : null}
            <Container>
                {/* {seconds === 10 ? sendOrder() : null}  */}
                <DrawingContainer color={border_user_color} cursor={cursor_status}>
                    <canvas id="draw" ref={canvasRef} width="610" height={'600'}></canvas>
                </DrawingContainer>
                {
                    (
                        possible === true
                            ?
                            seconds > 0 ? (
                                <img
                                    src={require('../assets/timer_' + seconds + '.png')}
                                    style={{
                                        width: '80px',
                                        height: '50px',
                                        backgroundSize: 'contain',
                                        marginTop: '20px',
                                        zIndex: '1',
                                        marginLeft: '-100px',
                                    }}
                                />
                            ) : (
                                ''
                            )
                            : '')
                }
            </Container>
            {/* <button onClick={onClick}></button> */}
            {/* <button onClick={saveCanvas}></button> */}
        </div>
    );
};

const Container = styled.div`
    background-color: #ffffff;
    width: 610px;
    height: 600px;
    display: flex;
    border-radius: 15px;
    
`;

const Toast = styled.div`
    background-color: #ffffff;
    width: 300px;
    height: 30px;
    display: flex;
    border-radius: 10px;
    position: absolute;
    margin-left: 220px;
    margin-top: -85px;
    align-items: center; 
    justify-content: flex-start;
    padding: 10px;
    color: black;
    box-shadow: 5px 5px 15px #808080;
`;

const TimerToast = styled.div`
    background-color: #ffffff;
    width: 120px;
    height: 30px;
    display: flex;
    border-radius: 10px;
    position: absolute;
    margin-left: 280px;
    margin-top: -85px;
    align-items: center; 
    justify-content: center;
    padding: 10px;
    color: black;
    box-shadow: 5px 5px 15px #808080;
`;

const DrawingContainer = styled.div`
    background-color: #ffffff;
    border-radius: 15px;
    border-width: 3px;
    border-style: solid;
    border-color: #ffffff;
    ${(props) => `box-shadow: 0px 0px 5px 5px ${props.color};`}
    ${(props) => (props.cursor === true ? `cursor: pointer;` : `cursor: not-allowed;`)}
    
    
`;

export default GameDrawing;