import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import ChatContext from '../components/ChatContext';

const Chatting = (props) => { 
    const [recentChatColor, setRecentChatColor] = useState(); //   
    
    //     
    const [inputMessage, setInputMessage] = useState('');

    //        
    const [emitMessage, setEmitMessage] = useState(false);

    //                    

    //      UI   
    const [chatMonitor, setChatMonitor] = useState([]);

    //       
    const [onMessage, setOnMessage] = useState(false);

    //     ( )   
    const [recentChat, setRecentChat] = useState("");

    //   ( )    
    const [recentChatUserName, setRecentChatUserName] = useState('');

    //   ( )    
    const [recentChatUserIdx, setRecentChatUserIdx] = useState('');

    //     
    let user_color = props.color; 
    
    if(user_color === 'RED'){
        user_color = '#FF0000';
    }else if(user_color === 'ORANGE'){
        user_color = '#FF5C00'
    }else if(user_color === 'YELLOW'){
        user_color = '#FFB800'
    }else if(user_color === 'GREEN'){
        user_color = '#95DB3B'
    }else if(user_color === 'BLUE'){
        user_color = '#3B8EDB'
    }else if(user_color === 'PINK'){
        user_color = '#CE3BDB'
    }else if(user_color === 'WHITE'){
        user_color = '#FFFFFF'
    }else{
        user_color = '#946CEE'
    }

    //   
    const handleInput = (e) => {
        let textValue = e.target.value.replace(/^\s*/, "");
        setInputMessage(textValue);
    };

    //  ,    
    const handleEnter = (e) => {
        if (e.key === 'Enter') {
            if (inputMessage.length > 0) {
                let msg = inputMessage;

                let getMsgInfo = {
                    user_color: user_color,
                    msg: msg
                }

                props.socket.emit("chat", getMsgInfo);

                setEmitMessage(!emitMessage);
                setInputMessage('');
            }
            
        }
    };

    //    ,    
    const handleSubmit = () => {
        if (inputMessage.length > 0) {
            let msg = inputMessage;

            let sendMsgInfo = {
                user_color: user_color,
                msg: msg
            }

            props.socket.emit("chat", sendMsgInfo);

            setInputMessage('');
            setEmitMessage(!emitMessage);
        }
    };

    //       (   )

    useEffect(() => {
        props.socket.on('connect', () => {
            console.log('chatting connection server');
        });
        
        props.socket.on("chat", (data) => {
            setRecentChatColor(data.user_color);
            setRecentChatUserName(data.user_name);
            setRecentChat(data.msg);
            setRecentChatUserIdx(data.user_idx);

            setOnMessage(true);

        });

        props.socket.on('exit room', (data) => {
            if(data.user_name !== undefined){
                let msg = data.user_name + "  ."

                setRecentChatColor('#fff');
                setRecentChatUserName(' ');
                setRecentChat(msg);
                setRecentChatUserIdx('00');

                setOnMessage(true);
            }
            
        });

    }, []);

    //    
    const scrollToBottom = () => {
        document.getElementById('chatMonitor').scrollBy({ top: 100 });
    };

    //  ,    , async, await         
    useEffect( () => {
        const scrollUpdate = async () => {
        //     
        (await recentChat.length) > 0 && setChatMonitor([...chatMonitor, {recentChat, recentChatUserName, recentChatColor}]); 
        
        // await    
        scrollToBottom();
        setRecentChat('');
        };

        scrollUpdate();
    }, [recentChat]);

  return (
    <React.Fragment>
            <Container style={{height : props.height}}>
                <ChatContainer id="chatMonitor">
                    {/* 18   */}
                    {chatMonitor.map((values, index) => {          
                        return (<ChatContext key={index} name={values.recentChatUserName} color={values.recentChatColor} text={values.recentChat}></ChatContext>);
                    })}
                </ChatContainer>
                <InputMsgContainer>
                {props.available?
                    <InputMsg
                        type="text"
                        placeholder="   "
                        value={inputMessage}
                        onChange={handleInput}
                        onKeyPress={handleEnter}
                    ></InputMsg> :
                    <InputMsg
                        type="text"
                        placeholder="   "
                        value={inputMessage}
                        onChange={handleInput}
                        onKeyPress={handleEnter}
                        disabled={true}
                    ></InputMsg>

                }
                    <InputMsgBtn onClick={handleSubmit}></InputMsgBtn>
                </InputMsgContainer>
            </Container>
    </React.Fragment>
);
};

const Container = styled.div`
    width: 220px;
    height: 620px;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    color: white;
    opacity: 1.5;
`;

const ChatContainer = styled.div`
    width: 200px;
    height: 520px;
    padding: 10px;
    margin-bottom: 20px;
    background-color: rgba( 0, 0, 0, 0.5 );
    border-radius: 0.5rem;
    // overflow-y: scroll;
    // overflow-x:hidden;
    overflow: auto;
    &::-webkit-scrollbar {
        /*    */
        width: 10px;

        /*    */
        height: 10px;

        border-radius: 6px;
        background: rgba(255, 255, 255, 0.4);
    }
    &::-webkit-scrollbar-thumb {
        background-color: rgba(0, 0, 0, 0.5);
        border-radius: 6px;
    }
`;

const InputMsgContainer = styled.div`
    width: 200px;
    height: 80px;
    padding: 10px;
    background-color: rgba( 0, 0, 0, 0.5 );
    border-radius: 0.5rem;
    display: flex;
    justify-content: space-between;
`;

const InputMsg = styled.textarea`
    width: 160px;
    height: 60px;
    padding: 10px;
    background-color: #000000;
    border-radius: 0.5rem;
    margin-right: 7px;
    border: none;
    outline: none !important;
    resize: none;

    &:hover {
        cursor: grab;
    }

    ::placeholder {
        color: white;
        font-weight: bold;
    }
    color: white;

    overflow-y: scroll;
    &::-webkit-scrollbar {
        /*    */
        width: 10px;

        /*    */
        height: 10px;

        border-radius: 6px;
        background: rgba(255, 255, 255, 0.4);
    }
    &::-webkit-scrollbar-thumb {
        background-color: rgba(0, 0, 0, 0.3);
        border-radius: 6px;
    }
`;

const InputMsgBtn = styled.div`
    width: 40px;
    height: 80px;
    background-color: #000000;
    border-radius: 0.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;

    &:hover {
        cursor: grab;
    }
`;

export default Chatting;