import React, { useEffect, useState, useRef } from 'react';
import style from '../styles/styles';
import styled from 'styled-components';
import { useSelector } from 'react-redux';
import UserVote from './UserVote';
import axios from 'axios';
import RefreshVerification from '../server/RefreshVerification';

// 
import { useHistory, useLocation, Prompt } from 'react-router';

const GameVoteComponent = (props) => {
    const userList = props.userList;
    const gameSet = props.gameSet;

    const [clicked, setClicked] = useState(false); // 
    const [voteWho, setVoteWho] = useState(''); //    
    //const [voteIndex, setVoteIndex] = useState(); //    
    const baseURL = useSelector((state) => state.socket.base_url);

    //  
    const voteIndex = useRef(-1); // orderCount

    // 10 
    const [seconds, setSeconds] = useState(10);

    // 
    let verify = RefreshVerification.verification()
    console.log('   t/f  : ' + verify);
    let data, save_token;

    if (verify === true) {
        data = sessionStorage.getItem('token');
        save_token = JSON.parse(data) && JSON.parse(data).access_token;
    }
    
    useEffect(() => {
        setClicked(false);
        setVoteWho('');
    }, []);

    useEffect(() => {
        if (props != null) {
            //       .
            const countdown = setInterval(() => {
                if (parseInt(seconds) > 0) {
                    setSeconds(parseInt(seconds) - 1);
                }

                if (parseInt(seconds) === 0) {
                    setSeconds(0);
                    postVote();
                }
            }, 1000);

            return () => {
                clearInterval(countdown);
            };
        }
    }, [seconds]);

    const postVote = async () => {
        //         api  
        //  api

        /* let str = -1;

        if (voteIndex !== null || voteIndex !== '')
            str = voteIndex.current; */

        const restURL = baseURL + 'game/vote';

        const reqHeaders = {
            headers: {
                authorization: 'Bearer ' + save_token,
            },
        };
        axios
            .post(
                restURL,
                {
                    game_set_idx: gameSet,
                    user_idx: voteIndex.current,
                },
                reqHeaders
            )
            .then(function (response) {
                console.log('postVote ');
            })
            .catch(function (error) {
                console.log('postVote ');
                console.log(error.response);
            });

        setSeconds(-1);
    };

    return (
        <Container>
            {/* <br /> */}
            ,    <text style={{ color: style.red, textShadow: '3px 3px #980000' }}></text> .
            <div style={styles.userListContainer}>
                {userList &&
                    userList.map((element) => (
                        <Div
                            onClick={() => {
                                setClicked(true);
                                setVoteWho(element.user_color);
                                //setVoteIndex(element.user_idx);
                                voteIndex.current = element.user_idx;
                                console.log('API: ? ' + gameSet + ', ? ' + voteIndex.current);
                            }}
                        >
                            <UserVote nick={element.user_name} color={element.user_color} click={clicked} voteWho={voteWho} />
                        </Div>
                    ))}
            </div>

            {
                seconds === 0 ?
                    <Info>
                          <InfoRed>  </InfoRed>.
                    </Info>
                    :
                    seconds > 0 ?
                        <Info>
                              <InfoRed> {seconds} </InfoRed>  .
                        </Info>
                        :
                        null //-1 -1   
            }

        </Container>
    );
};

const Container = styled.div`
    width: 580px;
    height: 500px;
    margin-top: 70px;
    border-radius: 10px;
    text-align: center;
    font-size: 30px;
    color: #fff;
    font-family: Black Han Sans;
`;

const Div = styled.div`
    &:hover {
        cursor: grab;
    }
`;

const Info = styled.text`
    font-size: 20px;
    color: #ffffff;
    margin-top: 60px;
    font-family: Black Han Sans;
`;

const InfoRed = styled.text`
    font-family: Hahmlet;
    -webkit-text-stroke: 1px ${style.red};
    color: ${style.red};
    3px 3px #980000
`;

const styles = {
    userListContainer: {
        display: 'flex',
        alignItems: 'flex-start',
        justifyContent: 'flex-start',
        flexDirection: 'column',
        width: '590px',
        height: '410px',
        flexFlow: 'row wrap',
        marginTop: '20px',
    },
};

export default GameVoteComponent;