import React, { useState } from 'react';
import styled from 'styled-components';
import style from '../../styles/styles';
import NightSVG from '../../assets/night.svg';
import molly from '../../assets/molly.png';
import holly from '../../assets/holly.png';
import { Link } from 'react-router-dom';
import tutorial_btn from '../../assets/tutorial_btn.svg';
import Start_button from '../../assets/start_button.svg';
import Modal from '../../components/ModalNickName';
import { useSelector } from 'react-redux';
import { useHistory } from 'react-router';

import Human from '../../assets/human.svg';
import PurpleCharacter from '../../assets/purple.svg';



export default function TutorialWorldviewComponent({}) {
    let data = localStorage.getItem('token');
    let save_token = JSON.parse(data) && JSON.parse(data).access_token;

    const isLogin = useSelector((state) => state.socket.is_login);

    const history = useHistory();
    function goRoomList() {
        history.push({
            pathname: '/roomlist', //    
        });
    }

    const moveRolePage = async (str) =>{
        //role   
        history.push({
            pathname: '/nicknameTutorial',
            state: { role: str },
        });
    }

    return (
        <Container>
            {/*       div */}
            <HumanDiv onClick={() => {moveRolePage("human");}}>
                <Div>
                    <MollyCharacter src={molly}></MollyCharacter>
                    <MollyTitle>
                          <text style={{ ...style, color: style.red }}></text>?
                    </MollyTitle>
                    <MollyText>
                            Molly!   ? <text style={{ ...style, color: style.red }}>3</text>
                              .
                        <br />            .    
                        !
                    </MollyText>
                    <br />
                </Div>

                <Link to="/">
                    <CardContainer>
                        <CharacterContainer className="ghost">
                            <HumanCharacter />
                        </CharacterContainer>
                    </CardContainer>
                </Link>
            </HumanDiv>

            {/*      div */}
            <GhostDiv onClick={() => {moveRolePage("ghost");}}>
                <Link to="/">
                    <CardContainer>
                        <CharacterContainer className="ghost">
                            <GhostCharacter />
                        </CharacterContainer>
                    </CardContainer>
                </Link>
                <Div>
                    <HollyCharacter src={holly}></HollyCharacter>
                    <HollyTitle>
                          <text style={{ ...style, color: style.red }}></text>?
                    </HollyTitle>
                    <HollyText>
                             Holly! ..  ! <br />
                            ,  ?          !   
                            . <text style={{ ...style, color: style.red }}>3</text>   
                           !
                    </HollyText>
                    <br />
                </Div>
            </GhostDiv>
            {isLogin ? (save_token === null ? <Modal tutorial /> : <GameStart src={Start_button} onClick={goRoomList} />) : <Modal tutorial />}
        </Container>
    );
}

const HumanDiv = styled.div`
    flex-direction: row;
    display: flex;
    // background-color: black;
    // justify-content: space-between;
    align-self: end;
    margin-right: 30px;
    margin-top: 30px;
`;

const GhostDiv = styled.div`
    flex-direction: row;
    display: flex;
    // background-color: black;
    // justify-content: space-between;
    // align-items: center;
    align-self: start;
    margin-left: 50px;
`;

const Container = styled.div`
    background-image: url(${NightSVG});
    width: 1020px;
    height: 620px;
    flex-direction: column;
    display: flex;
    background-color: black;
    // justify-content: space-between;
    // align-items: center;
    padding-top: 10px;
`;

const Div = styled.div`
    flex-direction: column;
    // background-color: pink;
    width: 450px;
    height: auto;
    display: flex;
    margin-right: 10px;
    margin-top: 15px;
`;

const MollyCharacter = styled.img`
    // background-color: ${style.skyblue};
    align-self: end;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: auto;
    width: 200px;
    margin-bottom: 5px;
    margin-right: 10px;
`;

const HollyCharacter = styled.img`
    // background-color: ${style.skyblue};
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: auto;
    width: 200px;
    margin-bottom: 10px;
    margin-right: 10px;
`;

const MollyTitle = styled.text`
    color: white;
    font-size: 23px;
    font-weight: light;
    // -webkit-text-stroke: 1px #c00202;
    margin-top: 5px;
    text-align: right;
    padding-right: 30px;
`;
const HollyTitle = styled.text`
    color: white;
    font-size: 23px;
    font-weight: light;
    // -webkit-text-stroke: 1px #c00202;
    margin-top: 5px;
    text-align: left;
`;

const MollyText = styled.text`
    color: white;
    font-size: 18px;
    font-weight: light;
    // -webkit-text-stroke: 1px #c00202;
    margin-top: 5px;
    text-align: right;
    padding-right: 30px;
`;
const HollyText = styled.text`
    color: white;
    font-size: 18px;
    font-weight: light;
    // -webkit-text-stroke: 1px #c00202;
    margin-top: 5px;
    text-align: left;
`;

const CardContainer = styled.div`
    margin-right: 30px;
    width: 180px;
    height: 260px;
    border: 2px solid #ffffff;
    background-color: transparent;
    border-radius: 0.5rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: 0px 0px 10px 10px rgba(0, 0, 0, 0.4); /* offset-x | offset-y | blur-radius | spread-radius | color */

    .ghost {
        animation: motion 1.5s linear 0s infinite;
        margin-bottom: 0;
    }

    @keyframes motion {
        0% {
            margin-bottom: 4px;
        }
        10% {
            margin-bottom: 7px;
        }
        20% {
            margin-bottom: 10px;
        }
        30% {
            margin-bottom: 12px;
        }
        40% {
            margin-bottom: 14px;
        }
        50% {
            margin-bottom: 16px;
        }
        60% {
            margin-bottom: 14px;
        }
        70% {
            margin-bottom: 12px;
        }
        80% {
            margin-bottom: 10px;
        }
        90% {
            margin-bottom: 7px;
        }
        100% {
            margin-bottom: 4px;
        }
    }

    &:hover {
        cursor: grab;

        border: 2px solid yellow;
        box-shadow: 0px 0px 10px 10px rgba(255, 255, 255, 0.4); /* offset-x | offset-y | blur-radius | spread-radius | color */
    }
`;

const CardText = styled.text`
    margin-top: 10px;
    font-size: 20px;
    font-family: Hahmlet;
    -webkit-text-stroke: 1px #53305e;
    font-weight: bold;
    color: #ffffff;
    text-shadow: 4px 4px 0px #53305e, 4px 4px 0px #53305e;
`;

const CharacterContainer = styled.div`
    width: 120px;
    height: 260px;
    background-color: transparent;
    border-radius: 0.5rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
`;

const HumanCharacter = styled.div`
    background-image: url(${Human});
    background-size: cover;
    width: 150px;
    height: 220px;
`;

const GhostCharacter = styled.div`
    background-image: url(${PurpleCharacter});
    background-size: cover;
    width: 160px;
    height: 230px;
`;

// 
const GameStart = styled.img`
    // background-color: ${style.yellow};
    justify-content: center;
    align-items: center;
    // height: 90px;
    width: 200px;
    margin-right: 30px;
    margin-top: -50px;
    align-self: end;
    &:hover {
        cursor: grab;
        box-shadow: 0px 0px 10px 10px rgba(0, 0, 0, 0.4); /* offset-x | offset-y | blur-radius | spread-radius | color */
    }
`;
