import React, { useState } from 'react';
import styled from 'styled-components';
import style from '../../styles/styles';
import { useSelector } from 'react-redux';
import { useHistory, useLocation} from 'react-router';

import waitingRoom from '../../assets/waitingRoom.png';

export default function TutorialRoomListComponent({}) {
    const isLogin = useSelector((state) => state.socket.is_login);

    const history = useHistory();
    let location = useLocation();

    const colorTxt = "   \n  7  ,      .\n       .";

    function onMove(){
        history.push({
            pathname: '/roleTutorial', 
            state: { role: location.state.role },          
        });
    } 

    return (
        <Container>

            <NextBtn onClick={onMove}>Next Page </NextBtn>

            <Info>
              <div className= "textDiv">    :      .<br></br>           .</div>
            1
            </Info>
           
            <Modify>
              <div className= "textDiv"> 
                   <br></br>         , <br></br>    . 
              </div>
            2
            </Modify>

            <Chatting>
              <div className= "textDiv">  :         . </div>
            3
            </Chatting>

            <ColorBar>
              <div className= "textDiv">
                {colorTxt.split("\n").map((i, key) => {
                        if(key === 0)
                            return <p key={key}>{i}<br></br> </p>;
                        else
                            return <p key={key}>{i}</p>;
                    })}
              </div>
            4
            </ColorBar>          

            <UserCard>
              <div className= "textDiv"> 
                :  , ,     .
              </div>
            5
            </UserCard>

            <Exit>
              <div className= "textDiv"> 
                :    .
              </div>
            6
            </Exit>

            <Ready>
              <div className= "textDiv"> 
                  :     . <br></br>
                      <br></br>
                    .    
              </div>
            7
            </Ready>

            <Description>     !</Description>
        </Container>
    );
}

const Container = styled.div`
    background-image: url(${waitingRoom});
    width: 1020px;
    height: 620px;
    flex-direction: column;
    display: flex;
    background-color: black;
    background-size: contain;
    background-repeat: no-repeat;
    justify-content: center;
    // justify-content: space-between;
    align-items: center;
`;

const Description = styled.div`
    width: 500x;
    height: 50px;
    position: absolute;
    margin-left: -200px;
    margin-top: 570px; 
    font-size: 30px;   
    padding: 5px;
    color: ${style.white};
    font-family: Hahmlet;
    -webkit-text-stroke: 1px ${style.yellow};
`;

const NextBtn = styled.div`
    width: 150px;
    height: 25px;
    border-radius: 30px;
    text-align: center;
    position: absolute;
    margin-left: 800px;
    margin-top: -700px;
    border: 2px solid #000;
    border-radius: 20px;     
    font-size: 21px;   
    padding: 5px;

    &:hover {
        background-color: #462456;;
        color: ${style.white};
        border: 2px solid #000;
        border-radius: 20px;    
        cursor: grab; 
    }

`;
const Info = styled.div`
    width: 20px;
    height: 20px;
    border-radius: 50px;
    text-align: center;
    padding: 10px;
    color: ${style.red};
    background-color: ${style.black};
    font-weight: bolder;
    position: absolute;
    margin-left: -525px;
    margin-top: -580px;

    &:hover .textDiv {
        background-color:  ${style.white};
        color: ${style.black};
        border: 2px solid #000;
        border-radius: 10px;        
        display: inline;
    }

    .textDiv{
        z-index: 1;
        overflow: hidden;
        position: absolute;  
        top: -45px;
        left: 30px;
        width: 450px;
        display: none;
        padding: 5px;
    }
`;  

