//   
import { createStore, combineReducers, applyMiddleware, compose } from "redux";
import thunk from "redux-thunk";
import { createBrowserHistory } from "history";
import { connectRouter } from "connected-react-router";

import socket from "./modules/socket"; //    

// redux-persist
import {persistReducer  } from 'redux-persist';
import storage from 'redux-persist/lib/storage';

export const history = createBrowserHistory();

//  persist config 
const persistConfig = {
  key: 'root', // reducer         key, root  
  storage: storage, // localstorage 
  //whitelist: ["socket"], //       ,  combineReducers   
};

const middlewares = [thunk.withExtraArgument({history: history})];

const rootReducer = combineReducers({
  socket: socket,
  router: connectRouter(history),
});

const persistedReducer = persistReducer(persistConfig, rootReducer); // rootReducer persist 

//   
const env = process.env.NODE_ENV;
//      logger  .
if (env === "development") {
  const { logger } = require("redux-logger");
  middlewares.push(logger);
}

// 
const composeEnhancers =
  typeof window === "object" && window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__
    ? window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__({
        // Specify extensions options like name, actionsBlacklist, actionsCreators, serialize...
      })
    : compose;


const enhancer = composeEnhancers(applyMiddleware(...middlewares));

let store = (initialStore) => createStore(persistedReducer, enhancer);

//export const store = createStore(persist, enhancer);

// store persisted   
//export const persistor = persistStore(store);

export default store();

